import React from 'react';
import Helmet from 'react-helmet'
//imported components
import Button from '@material-ui/core/Button';
import Header from '../features/header';
import Footer from '../features/footer';
import GalleryScroll from '../features/galleryScroll';




const Gallery = () => {
    return (
        <>
            <Helmet>
                <title>Lilac Hair Studio | Atlanta Hair Salon | Book a Hair Appointment</title>
                <meta name="description"
                content="Lilac Hair Studio, the best hair salon in Atlanta, GA (Buckhead area). Providing the best haircuts, great hairstyles, custom color, balayage, and quality treatment near you."/>
                <meta charset="utf-8" />
                <meta name="viewport" content="width=device-width, initial-scale=1" /> 
            </Helmet>
        <Header/>
        <section>
            <div className="content-box ">
                <h1 className="content content-header service-header">Hair Gallery</h1>
            </div>
            
            <GalleryScroll/>

            <div className="service-btn-box">
                <Button className='service-btn' variant="contained" color="primary" href='https://kristinmealor.glossgenius.com/services'>
                    Book Now
                </Button>
            </div>

        </section>
        <Footer/>
        </>
    )
}

export default Gallery;